                    <svg v-if="scope.row.os=='android'"t="1592449033680" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="8152" width="25" height="25">
                        <path d="M391.460571 275.968q9.142857 0 15.725714-6.582857t6.582857-15.725714-6.582857-15.725714-15.725714-6.582857-15.433143 6.582857-6.290286 15.725714 6.290286 15.725714 15.433143 6.582857zM632.539429 275.968q9.142857 0 15.433143-6.582857t6.290286-15.725714-6.290286-15.725714-15.433143-6.582857-15.725714 6.582857-6.582857 15.725714 6.582857 15.725714 15.725714 6.582857zM168.594286 381.147429q23.990857 0 41.179429 17.115429t17.115429 41.179429l0 245.686857q0 24.576-16.822857 41.691429t-41.398857 17.115429-41.691429-17.115429-17.115429-41.691429l0-245.686857q0-23.990857 17.115429-41.179429t41.691429-17.115429zM774.290286 391.972571l0 380.562286q0 26.258286-18.285714 44.544t-44.032 18.285714l-42.861714 0 0 129.682286q0 24.576-17.115429 41.691429t-41.691429 17.115429-41.691429-17.115429-17.115429-41.691429l0-129.682286-78.848 0 0 129.682286q0 24.576-17.115429 41.691429t-41.691429 17.115429q-23.990857 0-41.179429-17.115429t-17.115429-41.691429l-0.585143-129.682286-42.276571 0q-26.258286 0-44.544-18.285714t-18.285714-44.544l0-380.562286 524.580571 0zM641.682286 160.548571q61.147429 31.451429 97.718857 87.698286t36.571429 123.172571l-528.603429 0q0-66.852571 36.571429-123.172571t98.304-87.698286l-40.594286-74.825143q-4.022857-7.460571 2.852571-11.410286 7.460571-3.437714 11.410286 3.437714l41.179429 75.410286q54.272-23.990857 114.834286-23.990857t114.834286 23.990857l41.179429-75.410286q4.022857-6.875429 11.410286-3.437714 6.875429 4.022857 2.852571 11.410286zM914.285714 439.442286l0 245.686857q0 24.576-17.115429 41.691429t-41.691429 17.115429q-23.990857 0-41.179429-17.115429t-17.115429-41.691429l0-245.686857q0-24.576 17.115429-41.398857t41.179429-16.822857q24.576 0 41.691429 16.822857t17.115429 41.398857z" fill="#378f8c" p-id="8153"></path>
                    </svg>
                </template>
            </el-table-column>
            <el-table-column
                align="center"
                min-width="120"
                :label="$t('device.deviceName')"
                >
                <template slot-scope="scope"> 
                    <div class="pointer url" @click="goRouter(scope.row)" v-if="osArray.indexOf(scope.row.os)>-1&& scope.row.isonline">
                        {{scope.row.devName}}
                    </div>
                    <div class="pointer" v-else>
                        {{scope.row.devName}}
                    </div>
                </template>
            </el-table-column>
            <el-table-column
                align="center"
                prop="boardName"
                min-width="120"
                :label="$t('device.boardName')"
               >
            </el-table-column>
            <el-table-column
                align="center"
                prop="ipaddress"
                :label="$t('device.ipAddress')"
                min-width="120"
                >
            </el-table-column>

            <el-table-column
            column-key="isonline"
            prop="isonline"
            :label="$t('global.status')"
            min-width="120"
            align="center"
            :filters="[{ text: this.$t('device.online'), value: true }, { text: this.$t('device.offline'), value: false }]"
            filter-placement="bottom-end"
            >
                <template slot-scope="scope">
                    <div v-if="scope.row.isonline">
                        <i class="fa fa-child fa-x c-success"></i>
                        <span class="c-success">{{$t('device.online')}}</span>
                    </div>
                    <div v-else>
                        <i class="fa fa-child fa-x c-darkgray"></i>
                        <span class="c-darkgray">{{$t('device.offline')}}</span>
                    </div>
                    
                </template>
            </el-table-column>
            <!-- <el-table-column
                align="center"
                prop="os"
                :label="$t('device.os')"
                >
            </el-table-column> -->
            <el-table-column
                column-key="hardwarestatus"
                :filters="[{ text: $t('overview.normal'), value: 0 }, { text: $t('overview.warning'), value: 2 }, { text: $t('overview.error'), value: 1 }]"
                filter-placement="bottom-end"
                align="center"
                prop="hardwarestatus"
                :label="$t('device.hardware')"
                min-width="120"
                >
                <template slot-scope="scope">
                    <span v-if="scope.row.isonline==false" class="badge bg_gray">{{$t('overview.unknown')}}</span>
                    <span v-if="scope.row.hardwarestatus==0 && scope.row.isonline==true" class="badge bg_green">{{$t('overview.normal')}}</span>
                    <span v-if="scope.row.hardwarestatus==1 && scope.row.isonline==true" class="badge bg_red">{{$t('overview.error')}}</span>
                    <span v-if="scope.row.hardwarestatus==2 && scope.row.isonline==true" class="badge bg_orange">{{$t('overview.warning')}}</span>
                </template>
            </el-table-column>
            <el-table-column
                column-key="softwarestatus"
                :filters="[{ text: $t('overview.normal'), value: 0 }, { text: $t('overview.warning'), value: 2 }, { text: $t('overview.error'), value: 1 }]"
                filter-placement="bottom-end"
                align="center"
                prop="softwarestatus"
                :label="$t('device.software')"
                min-width="120"
                >
                <template slot-scope="scope">
                    <span v-if="scope.row.isonline==false" class="badge bg_gray">{{$t('overview.unknown')}}</span>
                    <span v-if="scope.row.softwarestatus==0 && scope.row.isonline==true" class="badge bg_green">{{$t('overview.normal')}}</span>
                    <span v-if="scope.row.softwarestatus==1 && scope.row.isonline==true" class="badge bg_red">{{$t('overview.error')}}</span>
                    <span v-if="scope.row.softwarestatus==2 && scope.row.isonline==true" class="badge bg_orange">{{$t('overview.warning')}}</span>
                </template>
            </el-table-column>
            <el-table-column
                width="120px"
                align="center"
                >
                <template slot-scope="scope">
                    <span v-if="scope.row.isonline&& osArray.indexOf(scope.row.os)>-1" class="fa fa-angle-right fa-2x m-r-10 pointer c-primary"></span>
                </template>
            </el-table-column>
        </el-table>
        <div class="m-t-10 cf fr">
                <el-pagination
                @current-change="handleCurrentChange"
                layout="prev, pager, next"
                :page-size="limit"
                :current-page="currentPage"
                :total="dataCount"
                v-show="isshow"
                background
                >
                </el-pagination>
            </div> 
    </div>    
</template>
<script>

import handleResponse from "../../components/restfulapi/handleResponse";
import {getDeviceByPageInGroupApi, 
        getDeviceByPageAndStatusInGroupApi,
        getDeviceByPageAndSoftwareStatusInGroupApi,
        getDeviceByPageAndHardwareStatusInGroupApi
        } from "../restfulapi/deviceApi";
import deviceStatusFunction from "../../assets/js/filter";
import {mapState} from 'vuex';
import {groupType} from "../../assets/js/constant"

export default {
    name: 'groupstatus',
    data() {
        return {
            deviceList:[],
            gname: "",
            statusArray: [],
            hardStatusArray: [],
            softStatusArray: [],
            currentPage: 1,
            limit: 10,
            dataCount: null,
            isshow: false,
            osArray: [groupType.android, groupType.linux, groupType.windows]
        }
    },

    methods: {
        handleCurrentChange(currentPage){
            if(currentPage){
                this.currentPage = currentPage;
            }else{
                this.currentPage = 1
            }
            this.getDeviceByPageInGroup();
        },
        filterIsOnline(value){
            if(value.isonline){
                this.clearIsOnlineFilter(['hardwarestatus','softwarestatus']);
                this.hardStatusArray = [];
                this.softStatusArray = [];
                this.statusArray = value.isonline;
                this.currentPage = 1;
                if(_g.isArray(this.statusArray)){
                    this.getDeviceByPageInGroup();
                }
            }else if(value.hardwarestatus){
                this.clearIsOnlineFilter(['isonline','softwarestatus']);
                this.statusArray = [];
                this.softStatusArray = [];
                this.hardStatusArray = value.hardwarestatus;
                this.currentPage = 1;
                if(_g.isArray(this.statusArray)){
                    this.getDeviceByPageInGroup();
                }
            }else{
                this.clearIsOnlineFilter(['isonline','hardwarestatus']);
                this.statusArray = [];
                this.hardStatusArray = [];
                this.softStatusArray = value.softwarestatus;
                this.currentPage = 1;
                if(_g.isArray(this.statusArray)){
                    this.getDeviceByPageInGroup();
                }
            }
        },
        clearIsOnlineFilter(array){
            this.$refs.filterTable.clearFilter(array);
        },
        getDeviceByPageInGroup(){
            if(this.statusArray.length>0){
                getDeviceByPageAndStatusInGroupApi(this.gname, this.currentPage, this.limit, this.statusArray).then((data) => {
                    handleResponse(data, (res) => { 
                        this.deviceList = res.data;
                        this.dataCount = res.count;
                        this.deviceList = res.data;
                        this.isshow = this.dataCount > this.limit;
                    })
                })
            }else if(this.softStatusArray.length>0){
                getDeviceByPageAndSoftwareStatusInGroupApi(this.gname, this.currentPage, this.limit, this.softStatusArray).then((data) => {
                    handleResponse(data, (res) => { 
                        this.deviceList = res.data;
                        this.dataCount = res.count;
                        this.deviceList = res.data;
                        this.isshow = this.dataCount > this.limit;
                    })
                })
            }else if(this.hardStatusArray.length>0){
                getDeviceByPageAndHardwareStatusInGroupApi(this.gname, this.currentPage, this.limit, this.hardStatusArray).then((data) => {
                    handleResponse(data, (res) => { 
                        this.deviceList = res.data;
                        this.dataCount = res.count;
                        this.deviceList = res.data;
                        this.isshow = this.dataCount > this.limit;
                    })
                })
            }else {
                getDeviceByPageInGroupApi(this.gname, this.currentPage, this.limit).then((data) => {
                    handleResponse(data, (res) => { 
                        this.deviceList = res.data;
                        this.dataCount = res.count;
                        this.deviceList = res.data;
                        this.isshow = this.dataCount > this.limit;
                    })
                })
            }
        },

        goRouter(row){
            if(row.isonline&& this.osArray.indexOf(row.os)>-1){
                this.$router.push({
                path: "/main/overview/devicestatus",
                query: {
                    deviceName: row.devName,
                    gname: this.gname,
                    endpoint: row.endpoint,
                    os: row.os
                    }
                })
            }
            
        },

        back(){
            this.$router.push("/main/overview/overview")
        }
    },
    created() {
        if(this.$route.query!=null){
            if(this.gname=this.$route.query.gname){
                this.getDeviceByPageInGroup()
            } 
        }else{
            console.log("the gname is null")
        }
    },
    watch:{
        onlineDeviceCount(){
            this.getDeviceByPageInGroup();
        },
        deviceMsgCount(){
            this.getDeviceByPageInGroup();
        },
        peripheralMsgCount(){
            this.getDeviceByPageInGroup();
        },
        deviceStatusCount(){
            this.getDeviceByPageInGroup();
        }

    },
    computed:{
        ...mapState({
            onlineDeviceCount: "onlineDeviceCount",
            deviceMsgCount: "deviceMsgCount",
            peripheralMsgCount: "peripheralMsgCount",
            deviceStatusCount: "deviceStatusCount",
        }),   
    },
}
</script>
<style lang="scss" scoped>
@import "../../assets/css/colors"; 

    .pointer{
        cursor: pointer;
    }
    .badge{
        display: inline-block;
        min-width: 10px;
        padding: 5px 8px;
        font-size: 12px;
        font-weight: 700;
        line-height: 1;
        color: #fff;
        text-align: center;
        white-space: nowrap;
        vertical-align: middle;
        // background-color: #777;
        border-radius: 10px;
    }
    .bg_gray{
        background-color: gray;
    }
    .bg_green{
        background-color: green;
    }
    .bg_orange{
        background-color: orange;
    }
    .bg_red{
        background-color: red;
    }
</style>



